.songs-wrapper {
  background: $white;
  .first-line {
    height: 50px;
    display: flex;
    align-items: center;
    .play-all {
      .playicon {
        display: inline-block;
        width: 58px;
        text-align: center;
        > svg {
          color: $primary;
          font-size: $font-size-lg;
          vertical-align: sub;
        }
      }
      > span {
        font-weight: bold;
        font-size: 17px;
        .sum {
          color: $description;
          font-size: $font-size-least;
        }
      }
    }
  }
}
.songs-ul {
  margin: 0;
  padding: 0;
  list-style: none;
  > li {
    display: flex;
    align-items: center;
    height: 60px;
    // margin-bottom: 5px;
    .index {
      flex-basis: 60px;
      width: 30px;
      height: 60px;
      line-height: 60px;
      text-align: center;
    }
    .song-item {
      width: 80%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      // border-bottom: 1px solid $border-color;
      span:nth-of-type(1) {
        padding-bottom: 10px;
      }
      .song-name {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .singer-name {
        color: $description;
        font-size: $font-size-least;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        > svg {
          font-size: $font-size-least;
          transform: scale(0.8);
          color: $danger;
        }
      }

      &.isIndent {
        margin-left: 30px;
      }
    }
  }
}
